import React, { useState, useEffect } from "react";
import { sendPost } from "./request";
import { Button, TextField, Box, Stack } from "@mui/material";
import { connect } from "react-redux";
import { useNavigate } from "react-router";
import Page from "./Page";
import { EditorState } from "draft-js";
import { convertToHTML } from "draft-convert";
import { Editor } from "react-draft-wysiwyg";

const NewPost = ({ accessToken }) => {
    const [editorState, setEditorState] = useState(() =>
        EditorState.createEmpty()
    );
    const [title, setTitle] = useState("");
    const navigate = useNavigate();

    const [convertedContent, setConvertedContent] = useState(null);

    useEffect(() => {
        setConvertedContent(convertToHTML(editorState.getCurrentContent()));
    }, [editorState]);

    const onTitleChange = (e) => {
        setTitle(e.target.value);
    };

    const onSubmit = () => {
        sendPost({ accessToken, content: convertedContent, title });
        navigate("/admin");
    };

    return (
        <Box
            sx={{ p: 2, border: "1px dashed grey", justifyContent: "center" }}
            className="post-editor"
        >
            <Stack spacing={2}>
                <TextField
                    label="Title"
                    value={title}
                    onChange={onTitleChange}
                ></TextField>
                <Editor
                    multiline
                    rows={15}
                    editorState={editorState}
                    onEditorStateChange={setEditorState}
                    wrapperClassName="wrapper-class"
                    editorClassName="editor-class"
                    toolbarClassName="toolbar-class"
                />
                <Button onClick={onSubmit} variant="contained">
                    Post
                </Button>
            </Stack>
        </Box>
    );
};

const mapStateToProps = (state) => ({
    accessToken: state.user.accessToken,
});

export default connect(mapStateToProps)(NewPost);
